import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { IOptionsPsEntityPhoneNumberExposed } from 'src/app/commonBussinessSRC/psComponents/ps-field-entity/ps-entity-phone-number/ps-entity-phone-number.component.interfaces';
import { IOptionsPsInputVarcharExposed } from 'src/app/commonBussinessSRC/psComponents/ps-keyin-input/ps-input-varchar/ps-input-varchar.component.interfaces';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IOptionsPsContainerPanel, IOptionsPsFileUploadComponent, IOptionsPsKeyinTextarea, IOptionsTemplateStepper } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from '../omni-base/omni-base.page';


@Component({
  selector: 'contact-bank',
  templateUrl: './contact-bank.page.html',
})
export class ContactBankPage extends OmniBasePage implements OnInit {

  public formGroup: FormGroup = new FormGroup({});

  public options: IOptionsTemplateStepper = {
    group: this.formGroup,
    submitOptions: {
      group: this.formGroup,
      submitServiceUrl: PsCommonBusinessSettings.serviceUrl.sendEmail,
    },
    requestObject: {},
    numberOfSteps: 1,
    namesofSteps: ['contanct_us_step1'],
    isHorizontalStepper: true,
    stepperName: 'contact_bank_stepper',
  };


  public panelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'contact_us_key',
    expanded: true
  };

  public nameOptions: IOptionsPsInputVarcharExposed = {
    labelKey: 'full_name_key',
    placeHolder: 'full_name_key',
    fcName: 'fullName',
    group: this.formGroup
  };
  public emailOptions: IOptionsPsInputVarcharExposed = {
    labelKey: 'email_key',
    placeHolder: 'email_key',
    fcName: 'email',
    group: this.formGroup
  };

  public phoneNumberOptions: IOptionsPsEntityPhoneNumberExposed = {
    fcName: 'phone',
    labelKey: 'phone_number_key',
    placeHolder: 'phone_number_key',
    group: this.formGroup
  };

  public subjectOptions: IOptionsPsInputVarcharExposed = {
    fcName: 'subject',
    labelKey: 'subject_key',
    placeHolder: 'subject_key',
    group: this.formGroup
  };

  fileUploadOptions: IOptionsPsFileUploadComponent = {
    multiple: true,
    fcName: 'uploadedFiles',
    group: this.formGroup,
    uploadIconName: 'attach'
  };

  commentsOptions: IOptionsPsKeyinTextarea = {
    labelKey: 'body_message_key',
    rows: '3',
    cols: '10',
    fcName: 'message',
    group: this.formGroup
  };



  constructor(public commonService: PsCommonService, public logger: LoggerService) {
    super();
  }

  ngOnInit() {
    super.init();
    this.baseFormGroup = this.formGroup;
    if (PsCommonSettings.isLoggedIn) {
      const userInfo = this.commonService.session.getValueOf(ConstantCommon.USERINFO);

      this.options.requestObject[this.emailOptions.fcName] = userInfo.omniUserVO.EMAIL;
      this.options.requestObject[this.nameOptions.fcName] = userInfo.customerInfoCO.customerName || userInfo.customerInfoCO.shortName;

      //TP #1382386 giving the phone number a value makes it appear in the summary when it's hidden from the admin    
      //jira-8402 - Make sure to validate validity from CUST to avoid phone number to appear in summary in hidden
      const mobileCust = this.commonProv.getElementValidations(this.phoneNumberOptions.fcName);
      if (mobileCust.IS_VISIBLE) {
        this.commonProv.setValInsideNestedObj(this.phoneNumberOptions.fcName, userInfo.omniUserVO.MOBILE_NUMBER, this.options.requestObject);
      }
      //End of Jira-8402
      if (this.options.requestObject[this.phoneNumberOptions.fcName] == undefined) {
        let phoneNbVal = userInfo.customerInfoCO.userMobileNumber;
        this.commonProv.setValInsideNestedObj(this.phoneNumberOptions.fcName, phoneNbVal, this.options.requestObject);
      }
      this.commonProv.copyObject(this.options.requestObject, {
        userCifEmail: userInfo.email || userInfo.omniUserVO.EMAIL
      }, false, true);
    }
  }
}
